import { requestWB as uniRequest, requestZT as ztRequest } from 'uni-request';
export default {
	name: 'create-comment',
	props: {
		showAddComment: {
			type: Boolean,
			default() {
				return false;
			}
		},
		tid: { // 售卖产品id
			type: String,
			default() {
				return ''
			}
		},
		openId: { // userId
			type: String,
			default() {
				return ''
			}
		},
		top: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			activeClass: false,
			commentVal: '',
			userId: this.openId
		};
	},
	methods: {
		closePage() {
			this.$emit('handleShowCom', false)
		},
		bindTextAreaBlur(e) {
			if(e.detail.value.length >= 15) {
				this.activeClass = true;
				this.commentVal = e.detail.value;
			} else {
				this.activeClass = false;
			}
		},
		// 发布操作
		handlePublic() {
			if(this.commentVal && this.commentVal.length >= 15) {
				// 请求发布评论接口
				ztRequest.post('/api/public/comment/add', {
					id: this.tid,
					openId: this.openId,
					content: this.commentVal
				}).then(res => {
					if(res.data.code == '000000') {
						uni.showToast({
							title: '发布成功'
						})
						this.commentVal = '';
						this.activeClass = false;
						
						this.$emit('handleShowCom', false);//关闭页面
						
					}else {
						uni.showToast({
							title: res.data.message
						})
					}
					
				})
				
			}else {
				return false;
			}
		}
	}
}
